    font-weight: bold;
    font-size: 20px;
  }
h1, h2, h3 {
  color: white;
  display: flex;
  justify-content: center;
  justify-items: center;
  
  border: 1px solid #ccc;
  
  background-color: rgb(71, 209, 106);
}
ul li a {
